const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

const inputTheme = document.querySelector('#theme-switch-toggle');
const bodyTheme = document.querySelector('body');
// bodyTheme.classList.add(Theme.LIGHT);



inputTheme.addEventListener('click', switchMode());

function switchMode(e) {
  if (bodyTheme.classList !== Theme.DARK ) {
    bodyTheme.classList.remove(Theme.LIGHT);
    bodyTheme.classList.add(Theme.DARK);
    localStorage.setItem('class', Theme.DARK);
  } else {
    bodyTheme.classList.add(Theme.LIGHT);
    localStorage.setItem('class', Theme.LIGHT);
  }
};

window.onload = function () {
  localStorage.getItem('class', Theme.DARK) ? bodyTheme.classList.add(Theme.DARK) : bodyTheme.classList.add(Theme.LIGHT)
}

console.log('bodyTheme', bodyTheme);
console.log('localStorage', localStorage.getItem);